body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-SearchBox {
  margin-top: 30px;
}

.App-WeatherBox {
  margin-top: 40px;
}

.hide {
  opacity: 0.2;
  position: absolute;
  top: 399px;
  left: 0px;
}

.hint {
  text-align: left;
  font-size: 9px;
}
.area {
    /* stroke: #ffcd00;
    stroke-width: 2px; */
}

.line {
    stroke: #ffcd00;
    stroke-width: 2px;
    fill: none;
}
